export default function MusicPage() {
  const [isClient, setIsClient] = useState(false);
  const [currentCategory, setCurrentCategory] = useState<string>('All');
  const [activeVideo, setActiveVideo] = useState<MusicVideo>(musicVideos[0]);
  const [key, setKey] = useState(0);
  const [isPlaying, setIsPlaying] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const playerRef = useRef<HTMLDivElement>(null);
  const videoGridRef = useRef<HTMLDivElement>(null);
  const heavenListRef = useRef<HTMLDivElement>(null);
  const [isHeaderVisible, setIsHeaderVisible] = useState(true);

  useEffect(() => {
    setIsClient(true);
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (currentScrollY > 100) {
        setIsHeaderVisible(false);
      } else {
        setIsHeaderVisible(true);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const handleVideoSelect = (video: MusicVideo) => {
    setActiveVideo(video);
    setKey(prev => prev + 1);
    setIsPlaying(true);
    playerRef.current?.scrollIntoView({ behavior: 'smooth', block: 'center' });
  };

  const togglePlayback = () => {
    setIsPlaying(!isPlaying);
  };

  const handleCategoryClick = (category: string) => {
    setCurrentCategory(category);
    
    // Scroll to appropriate section with offset for sticky header
    setTimeout(() => {
      const headerOffset = isHeaderVisible ? 100 : 60; // Adjust offset based on header visibility
      if (category === 'Heaven List') {
        heavenListRef.current?.scrollIntoView({ behavior: 'smooth' });
        window.scrollBy(0, -headerOffset);
      } else {
        videoGridRef.current?.scrollIntoView({ behavior: 'smooth' });
        window.scrollBy(0, -headerOffset);
      }
    }, 100);
  };

  const categories = ['All', 'Heaven List', ...new Set(musicVideos.map(video => video.category))];
  const filteredVideos = currentCategory === 'All' 
    ? musicVideos 
    : musicVideos.filter(video => video.category === currentCategory);

  if (!isClient) {
    return <div>Loading...</div>;
  }

  return (
    <main 
      className="container mx-auto px-4 py-8"
      style={{
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundAttachment: 'fixed'
      }}
    >
      {/* Title with Custom SVG Logo */}
      <div className="flex items-center justify-center gap-5 mb-8">
        <Image 
          src="\images\Cheshire_head.svg" 
          alt="Music icon"
          width={78}
          height={78}
          className="animate-bounce"
        />
        <h1 className="text-3xl font-bold text-center">Music Gallery</h1>
        <Image 
          src="\images\Cheshire_head.svg" 
          alt="Music icon"
          width={78}
          height={78}
          className="animate-bounce"
        />
      </div>
      
      {/* Active Video Player */}
      <div className="mb-8" ref={playerRef}>
        <div className="relative aspect-video w-full max-w-4xl mx-auto rounded-xl overflow-hidden shadow-lg bg-black">
          {isLoading && (
            <div className="absolute inset-0 flex items-center justify-center bg-black/50">
              <div className="animate-spin rounded-full h-12 w-12 border-4 border-white border-t-transparent"></div>
            </div>
          )}
          <iframe
            key={key}
            src={`https://www.youtube.com/embed/${activeVideo.youtubeId}?autoplay=${isPlaying ? 1 : 0}&rel=0&playsinline=1&origin=${window.location.origin}`}
            title={activeVideo.title}
            className="absolute inset-0 w-full h-full"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; playsinline"
            allowFullScreen
            onLoad={() => setIsLoading(false)}
            onLoadStart={() => setIsLoading(true)}
          />
        </div>
        <div className="flex items-center justify-between mt-4">
          <h2 className="text-xl font-semibold">{activeVideo.title}</h2>
          <button
            onClick={togglePlayback}
            className="p-2 rounded-full hover:bg-gray-100"
            aria-label={isPlaying ? 'Pause' : 'Play'}
          >
            {isPlaying ? 'Pause' : 'Play'}
          </button>
        </div>
      </div>

      {/* Category Filter - Sticky Header */}
      <div className={`
        sticky ${isHeaderVisible ? 'top-16' : 'top-0'} 
        z-20 -mx-4 px-4 py-4 
        bg-white/95 backdrop-blur-sm shadow-sm
        transition-all duration-300
      `}>
        <div className="container mx-auto">
          <div className="flex flex-nowrap gap-3 overflow-x-auto pb-2 scrollbar-hide">
            {categories.map(category => (
              <button
                key={category}
                onClick={() => handleCategoryClick(category)}
                className={`
                  px-4 py-2 rounded-lg transition-all duration-200 whitespace-nowrap
                  ${currentCategory === category
                    ? 'bg-gray-900 text-white'
                    : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
                  }
                `}
              >
                {category}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Video Grid */}
      <div 
        ref={videoGridRef}
        className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 pt-4"
      >
        {filteredVideos.map((video) => (
          <div
            key={video.id}
            onClick={() => handleVideoSelect(video)}
            className={`relative aspect-video cursor-pointer group overflow-hidden rounded-lg bg-gray-100 ${
              activeVideo.id === video.id ? 'ring-2 ring-gray-900' : ''
            }`}
          >
            <div className="relative w-full h-full">
              <Image
                src={`https://img.youtube.com/vi/${video.youtubeId}/hqdefault.jpg`}
                alt={video.title}
                fill
                className="object-cover transition-transform duration-300 group-hover:scale-110"
                sizes="(max-width: 640px) 100vw, (max-width: 768px) 50vw, (max-width: 1024px) 33vw, 25vw"
              />
            </div>
            <div className="absolute inset-0 bg-black/30 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-center justify-center">
              <FaPlay className="w-12 h-12 text-white" />
            </div>
            <div className="absolute bottom-0 left-0 right-0 p-4 bg-gradient-to-t from-black/80 to-transparent">
              <h3 className="text-white font-medium truncate">{video.title}</h3>
            </div>
          </div>
        ))}
      </div>

      {/* Heaven List Section */}
      <div 
        ref={heavenListRef}
        className="mt-16 pt-16 border-t border-gray-200"
      >
        <div className="bg-white/80 backdrop-blur-sm rounded-xl shadow-lg p-6">
          <div className="flex items-center gap-3 mb-6">
            <FaFolder className="w-6 h-6 text-gray-600" />
            <h2 className="text-2xl font-bold">Heaven List</h2>
            <span className="text-2xl">üå†</span>
          </div>
          
          <div className="space-y-2">
            {heavenList.map((item) => (
              <div 
                key={item.id}
                className="group border-b border-gray-100 last:border-0 py-2"
              >
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-2 min-w-0">
                    <span className="text-xl flex-shrink-0">{item.sticker}</span>
                    <div className="truncate">
                      {item.link ? (
                        <a 
                          href={item.link}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="font-medium hover:text-blue-600 transition-colors"
                        >
                          {item.title}
                        </a>
                      ) : (
                        <span className="font-medium">{item.title}</span>
                      )}
                      <span className="mx-2 text-gray-400">‚Ä¢</span>
                      <span className="text-gray-600">{item.artist}</span>
                    </div>
                  </div>
                  <p className="text-sm text-gray-500 hidden group-hover:block transition-all">
                    {item.description}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </main>
  );
}